import React from 'react'
import '../assets/style/list.sass'
import {Link} from 'gatsby'

const ListTemplate = ({
    posts,
    title,
    type
  }) => {

    const Posts = posts.length ? posts.map(({node: {title, slug, acf}}, index) => (
        <div className="big-slider__item" key={index}>
            <div className="big-slider__item__logo">
                {acf.logo ? <img src={acf.logo.source_url} alt="logo" /> : null}
            </div>
            <div className="big-slider__item__title">
                {title}
            </div>
            <Link to={`${type}/${slug}`} className="big-slider__item__link">
                Узнать подробнее
            </Link>
        </div>
    )) : 'Ничего не найдено'

    return (
        <div className="post-list">
          <div className="container">
            <div className="breadcrumbs">
              <Link to="/" href="">Главная</Link> /
            </div>
            <div className="post-list__title">
                {title} <span>{posts.length}</span>
            </div>
            <div className="post-list__items">
                {Posts}
            </div>
          </div>
        </div>
    )
  }

export default ListTemplate;